.maindiv {
  width: 100%;
  background-color: yellow;
  min-height: 100vh;
  margin-top: unset;
}
.mnginvnav{
  padding:  0% 3%;
  background-color: #3a8a9e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 5px black;
}
.mnginvnav > div:nth-of-type(1) {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 25%;
}
.mnginvnav > div:nth-of-type(1) > div:nth-of-type(1){
  margin-right: 20px;
}
.head{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.733);
}
.desdiv{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 25%;
}
.desdiv > i{
  color: yellow;
  font-size: 3rem
}
.teamImg > img {
  width: 100%;
  height: 50%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.firstName {
  margin-right: 5%;
}
.contentArea {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}
.TeamHolder {
  padding-top: 3%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 90%;
  margin-top: 0 auto;
}

.TeamDetail {
  width: 90%;
  background: rgb(130, 196, 226);
  padding: 5px;
  border: 4px double grey;
  box-shadow: 0px 0px 5px black;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  border-radius: 20px;
  overflow: hidden;
}
.TeamDetail:hover {
  background-color: rgb(54, 125, 184);
  border: 4px solid rgb(16, 87, 116);
}
.memdetail {
  padding: 0 3%;
}

.buttonClassaz {
  font-size:15px;
  font-family:Arial;
  width:100px;
  height:40px;
  border-width:1px;
  color:#3a8a9e;
  border-color:#d6bcd6;
  font-weight:bold;
  border-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background:linear-gradient(#ededed, #bab1ba);
  padding: unset;
}

.buttonClassaz:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
.buttonClass2{
  font-size: 11px;
  font-family: Arial;
  width: 90px;
  height: 30px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  margin: 2% 5px;
}

.buttonClass2:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
.addMember {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 40vh;
  padding: 0 1%;
  box-shadow: 0px 0px 5px grey;
  background-color: white;
  z-index: 5;
  position: absolute;
  left: 45%;
  top: 13%;
  border-radius: 20px;
  display: none;
}
.addform {
  border: none;
  outline: none;
  box-shadow: 0 0 5px black;
  background-color: yellow;
}
