.maindiv {
    width: 100%;
    height: 110vh;
    margin-top: unset;
    background-color: yellow;
  }
  .mnginvnav{
    padding:  0% 3%;
    background-color: #3a8a9e;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 0px 5px black;
}
.mnginvnav > div:nth-of-type(1) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 35%;
}
.mnginvnav > div:nth-of-type(1) > div:nth-of-type(1){
    margin-right: 20px;
}
.desdiv{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 25%;
}
.desdiv > i{
    color: yellow;
    font-size: 3rem
}
  .formdiv{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 1%;
      width: 50%;
      margin: 3% auto;
      box-shadow:0 0 5px black;
      background-color: #3a8a9e;
      border-radius: 50px;
  }
  form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 55vh;
    width: 95%;
  }
.stock1,.medName1,.mafName1,.price1,.discount1{
    width: 80%;
    font-size: 20px;
    border: none;
    outline: none;
    box-shadow: 0 0 5px white;
    background-color: transparent;
    margin-top: 0;
    padding: 5px;
    border-radius: 15px;
    }
  
  .buttonDiv{
    margin-top: 1% auto;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 70%;
  }
  .buttonClass4{
    font-size:15px;
    font-family:Arial;
    width:100px;
    height:40px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-radius: 15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);

  padding:unset;
  }
  
  .buttonClass4:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }