.maindiv{
    background:yellow;
    width: 100%;
    min-height: 100vh;
    height: max-content;
    margin-top: unset;
}
.mnginvnav{
    padding:  0% 3%;
    background-color: #3a8a9e;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 0px 5px black;
}
.mnginvnav > div:nth-of-type(1) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 25%;
}
.mnginvnav > div:nth-of-type(1) > div:nth-of-type(1){
    margin-right: 20px;
}
.head{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    color: rgba(0, 0, 0, 0.733);
}
.desdiv{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 25%;
}
.desdiv > i{
    color: yellow;
    font-size: 3rem
}
.contentArea{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction:column ;
}
.medHolder{
    padding-top: 3%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    width:80%;
    margin-top: 0 auto;
}
 
.itemDetail{
    background:rgb(56, 154, 179);
    box-shadow: 0 0 5px black;
    padding: 2% 3%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 3% auto;
    border-radius:20px ;
    color: rgb(8, 8, 8);
    text-shadow:  0 0 1px black;
}
.itemDetail:hover{
background-color: blanchedalmond;
border: 4px solid darkcyan;
}
#invNo{
    display: flex;
    width: 80%;
    background-color:rgb(217, 255, 0);
    justify-content: space-evenly;
    align-items: center;
}

.buttonClassbz {
    font-size:15px;
    font-family:Arial;
    width:100px;
    height:40px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-radius: 15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    padding: unset;
  }
  
  .buttonClassbz:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  .buttonClass1 {
    font-size:11px;
    font-family:Arial;
    width:70px;
    height:30px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-radius: 15px;
    box-shadow: 2px 2px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    margin: 0 2px;
  }
  
  .buttonClass1:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  .Medadditem{
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      height: 40vh;
      padding: 0 1%;
      box-shadow: 0px 0px 5px grey;
      background-color: rgb(248, 250, 244);
      z-index: 5;
      position: absolute;
      left: 42%;
      top: 15%;
      display: none;
      border-radius: 20px;
  }
  .addform{
      border: none;
      outline: none;
      box-shadow: 0 0 5px black;
      background-color: transparent;
  }